import React from 'react'

import Layout from '../components/layout'
import SEO from '../components/seo'
import {graphql} from 'gatsby'
import '../styles/lesson.scss'
import Newsletter from '../components/newsletter'
import '../styles/lesson.scss'
import { Link } from 'gatsby-plugin-modal-routing'

export default function Template({data}) {
    const {LessonQuery: lesson} = data;

    const title = lesson.frontmatter.title + ' | ' + lesson.frontmatter.course_title;

    return (
        <Layout cssCLass="lesson">
            <SEO title={ title } />
            <div className="courses-header pb-5 pt-5">
                <div className="container">
                    <span className="course-subtitle">Lesson {lesson.frontmatter.order_number}</span>
                    <h1 className="mb-0">{lesson.frontmatter.title}</h1>
                    <span className="course-subtitle">Course: {lesson.frontmatter.course_title}</span>
                </div>
            </div>
            <div className="container-fluid mb-5 mt-0">
                <div className="content-container pb-2">
                    <div className="row">
                        <div className="col-8">
                            <div dangerouslySetInnerHTML={{__html: lesson.html}} />
                        </div>
                        <div className="col-4">
                            <div className="sidebar mt-4">
                                <div className="sidebar-content p-3 mt-4">
                                    <div className="row ml-1 lesson-resources">
                                        <span className="material-icons mr-2 pt-1">cloud_download</span>
                                        <h4>Lesson Resources</h4>
                                    </div>
                                    {lesson.frontmatter.lesson_link !== '' ?
                                    <ul className="pt-1">
                                        <li><a href={lesson.frontmatter.lesson_link} className="download-code">{lesson.frontmatter.lesson_label}</a></li>
                                    </ul>
                                        : 'There are no resources for this lesson.'
                                    }
                                </div>
                                <div className="p-4 mt-3 newsletter-container">
                                    <Newsletter />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="navbar lesson-nav">
                <div className="col left">
                        {lesson.frontmatter.order_number > 1 ?
                          <Link to={lesson.frontmatter.prev}
                                className="btn btn-block text-right btn-left"
                                role="button"
                                aria-pressed="true">
                              <div className="button-label">
                                  <span className="material-icons arrow-icon arrow-left">keyboard_arrow_left</span>
                                  <span className="label-text">Previous Lesson</span>
                              </div>
                          </Link> :
                          <Link to={lesson.frontmatter.prev}
                                className="btn btn-block text-right btn-left"
                                role="button"
                                aria-pressed="true">
                              <div className="button-label">
                                  <span className="material-icons arrow-icon arrow-left">keyboard_arrow_left</span>
                                  <span className="label-text">Back to Course</span>
                              </div>
                          </Link>
                        }
                </div>
                <div className="col right">
                        {lesson.frontmatter.next !== '' ?
                          <Link to={lesson.frontmatter.next} className="btn btn-block btn-right text-left" role="button" aria-pressed="true">
                              <div className="button-label">
                                <span className="label-text">Next Lesson</span>
                                <span className="material-icons arrow-icon arrow-right">keyboard_arrow_right</span>
                              </div>
                          </Link> :
                          <Link to="/subscribe"
                                className="btn btn-block btn-right text-left" role="button" aria-pressed="true"
                                asModal state={{
                                  closeTo: lesson.frontmatter.path,
                                  noScroll: true
                              }}>
                              <div className="button-label">
                                  <span className="label-text">Next Lesson</span>
                                  <span className="material-icons arrow-icon arrow-right">keyboard_arrow_right</span>
                              </div>
                          </Link>
                        }
                </div>
            </div>
        </Layout>
    )
}

export const pageQuery = graphql`
    query($path: String!) {
      LessonQuery: markdownRemark(
        frontmatter: { 
          path: { eq: $path } 
        }
      ) {
        html
        frontmatter {
          path
          title
          course_title
          next
          prev
          order_number
          lesson_link
          lesson_label
        }
      }
    }
`
